import {Component} from '@angular/core';
import {IonicPage, NavController, NavParams} from 'ionic-angular';
import {PledgesProvider} from "../../../providers/pledges/pledges";
import {PledgesModel} from "../../../models/pledges-model";
import {AngularFireAuth} from 'angularfire2/auth';
import {FunctionsProvider} from "../../../providers/functions/functions";



@IonicPage()
@Component({
  selector: 'page-chat',
  templateUrl: 'pledges.html',
})
export class Pledges {

    pledges: PledgesModel[];
    private bid: any="1";
   // private uid: any = 'ff';


    constructor(navParams: NavParams,
                public navCtrl: NavController,
                private pledge: PledgesProvider,
                public afs: AngularFireAuth,
                private func: FunctionsProvider,

    ) {

this.loadData()

    }

    async loadData(){
        this.afs.authState.subscribe(res => {
            if (res && res.uid) {
               // this.func.sync(res.uid,this.bid)

                this.pledge.query(res.uid).then((res: any) => {

                    this.pledges = JSON.parse(res.data)


                }).catch(er=> console.log(er.message))
                ;


            } else {

                this.navCtrl.setRoot('login',{
                    refresh:"true",
                });
            }
        });

    }



openCell(obj){


}





}



